import React, { useCallback } from "react";
import {
  Chip,
  IconButton,
  Stack,
  Table,
  TableBody,
  TableCell,
  TableContainer,
  TableHead,
  TableRow,
} from "@mui/material";
import { EditOutlined, DeleteOutlined } from "@ant-design/icons";
import { Link } from "react-router-dom";

import { useAppDispatch } from "../../store/hooks";
import { IProject } from "../../_interfaces/project.interface";
import { deleteProject } from "../../store/projectsSlice";
import dayjs from "dayjs";
import Swal from "sweetalert2";
import withReactContent from "sweetalert2-react-content";
const MySwal = withReactContent(Swal);

interface IProjectsDataProps {
  ITEMS_PER_PAGE: number;
  data: IProject[];
  currentPage: number;
  openEditModal: (project: IProject) => void;
}
const ProjectsDataTable = ({
  data,
  ITEMS_PER_PAGE,
  currentPage,
  openEditModal,
}: IProjectsDataProps) => {
  const dispatch = useAppDispatch();

  const handelDeleteProject = useCallback(
    (data: IProject) => {
      MySwal.fire({
        title: `      ${data.projectName}?`,
        text: "      ",
        icon: "warning",
        showCancelButton: true,
        confirmButtonColor: "#3085d6",
        cancelButtonColor: "#d33",
        confirmButtonText: ", !",
        cancelButtonText: "",
      }).then((result) => {
        if (result.isConfirmed) {
          dispatch(deleteProject({ projectId: Number(data.id) }));
          MySwal.fire({
            title: "!",
            text: `${data.projectName}    `,
            icon: "success",
            confirmButtonText: "!",
          });
        }
      });
    },
    [dispatch]
  );

  return (
    <>
      <TableContainer>
        <Table sx={{ minWidth: 350 }} aria-label="simple table">
          <TableHead>
            <TableRow>
              <TableCell sx={{ pl: 3 }}>#</TableCell>
              <TableCell sx={{ pl: 3 }}> </TableCell>
              <TableCell> </TableCell>
              <TableCell></TableCell>
              <TableCell></TableCell>
              <TableCell align="right"></TableCell>
              <TableCell align="center"> </TableCell>
              <TableCell align="center"></TableCell>
              <TableCell align="center"></TableCell>
              <TableCell align="center"> </TableCell>
              <TableCell align="center" sx={{ pr: 3 }}>
                
              </TableCell>
            </TableRow>
          </TableHead>
          <TableBody>
            {data.map((row, index) => (
              <TableRow hover key={index}>
                <TableCell>
                  {(currentPage - 1) * ITEMS_PER_PAGE + (index + 1)}
                </TableCell>
                <TableCell sx={{ pl: 3 }}>
                  <Link
                    to={`/Projects/${row.id}`}
                    style={{ textDecoration: "none", color: "inherit" }}
                  >
                    {row.projectName}
                  </Link>
                </TableCell>
                <TableCell>{row.clientPhone}</TableCell>
                <TableCell align="center">{row.location}</TableCell>
                <TableCell>{row.quotation}</TableCell>
                <TableCell align="right">{row.paid}</TableCell>
                <TableCell align="center">
                  {row.unPaid != undefined && (
                    <Chip
                      color={"error"}
                      label={row.unPaid}
                      // label={row.quotation ? row.quotation - row!.paid : row.quotation}
                      size="small"
                    />
                  )}
                </TableCell>
                <TableCell align="right">{row.haregem}</TableCell>
                <TableCell align="right">{row.quotation}</TableCell>
                <TableCell align="right">
                  {dayjs(row.createdAt).format("DD-MM-YYYY")}
                </TableCell>

                <TableCell align="center" sx={{ pr: 3 }}>
                  <Stack
                    direction="row"
                    justifyContent="center"
                    alignItems="center"
                  >
                    <IconButton
                      color="primary"
                      size="large"
                      onClick={() => openEditModal(row)}
                    >
                      <EditOutlined />
                    </IconButton>
                    <IconButton
                      color="inherit"
                      size="large"
                      onClick={() => handelDeleteProject(row)}
                    >
                      <DeleteOutlined />
                    </IconButton>
                  </Stack>
                </TableCell>
              </TableRow>
            ))}
          </TableBody>
        </Table>
      </TableContainer>
    </>
  );
};

export default ProjectsDataTable;
